// Cache for API responses
let cachedModelProviders: ModelProvider[] | null = null;
let cachedVoiceProviders: VoiceProvider[] | null = null;
let cachedTranscriberProviders: TranscriberProvider[] | null = null;

// Fetch models from VAPI API and filter for OpenAI GPT-4 only
export const fetchModelProviders = async (): Promise<ModelProvider[]> => {
  if (cachedModelProviders) {
    return cachedModelProviders;
  }

  try {
    const response = await axiosInstance.get('/api/models/available');
    const vapiModels = response.data?.data || {};

    // Filter to only show OpenAI provider with GPT-4 model
    const openaiModels = vapiModels.openai || [];
    const gpt4Model = openaiModels.find((model: any) => model.id === 'gpt-4');

    if (gpt4Model) {
      cachedModelProviders = [
        {
          id: 'openai',
          name: 'OpenAI',
          description: 'Industry-leading language models with excellent reasoning capabilities',
          models: [
            {
              id: gpt4Model.id,
              name: gpt4Model.name || 'GPT-4',
              description: gpt4Model.description || 'Most capable model with superior reasoning',
              maxTokens: 8192,
              supportsTools: true,
              supportsVision: false,
              costTier: 'high'
            }
          ],
          defaultModel: 'gpt-4',
          requiresApiKey: false
        }
      ];
    } else {
      // Fallback if GPT-4 not found in VAPI response
      cachedModelProviders = [
        {
          id: 'openai',
          name: 'OpenAI',
          description: 'Industry-leading language models with excellent reasoning capabilities',
          models: [
            {
              id: 'gpt-4',
              name: 'GPT-4',
              description: 'Most capable model with superior reasoning',
              maxTokens: 8192,
              supportsTools: true,
              supportsVision: false,
              costTier: 'high'
            }
          ],
          defaultModel: 'gpt-4',
          requiresApiKey: false
        }
      ];
    }
  } catch (error) {
    console.error('Error fetching models from VAPI:', error);
    // Fallback to static configuration
    cachedModelProviders = [
      {
        id: 'openai',
        name: 'OpenAI',
        description: 'Industry-leading language models with excellent reasoning capabilities',
        models: [
          {
            id: 'gpt-4',
            name: 'GPT-4',
            description: 'Most capable model with superior reasoning',
            maxTokens: 8192,
            supportsTools: true,
            supportsVision: false,
            costTier: 'high'
          }
        ],
        defaultModel: 'gpt-4',
        requiresApiKey: false
      }
    ];
  }

  return cachedModelProviders;
};

// Fetch voices from VAPI API and filter for your specified voices only
export const fetchVoiceProviders = async (): Promise<VoiceProvider[]> => {
  if (cachedVoiceProviders) {
    return cachedVoiceProviders;
  }

  try {
    const response = await axiosInstance.get('/api/voices/available');
    const vapiVoices = response.data?.data || {};

    // Always return VAPI provider with your specified voices
    // We use the allowed voices list regardless of what VAPI returns
    cachedVoiceProviders = [
      {
        id: 'vapi',
        name: 'VAPI',
        description: 'Native VAPI voices with optimized performance',
        supportsSpeed: true,
        supportsStability: false,
        supportsSimilarityBoost: false,
        voices: ALLOWED_VAPI_VOICES.map(voice => ({
          ...voice,
          preview: `https://www2.cs.uic.edu/~i101/SoundFiles/BabyElephantWalk60.wav` // Default preview
        })),
        defaultVoice: 'Cole',
        requiresApiKey: false
      }
    ];
  } catch (error) {
    console.error('Error fetching voices from VAPI:', error);
    // Fallback to static configuration with your specified voices
    cachedVoiceProviders = [
      {
        id: 'vapi',
        name: 'VAPI',
        description: 'Native VAPI voices with optimized performance',
        supportsSpeed: true,
        supportsStability: false,
        supportsSimilarityBoost: false,
        voices: ALLOWED_VAPI_VOICES.map(voice => ({
          ...voice,
          preview: `https://www2.cs.uic.edu/~i101/SoundFiles/BabyElephantWalk60.wav`
        })),
        defaultVoice: 'Cole',
        requiresApiKey: false
      }
    ];
  }

  return cachedVoiceProviders || [];
};

